.main-fieldset {
    border-radius: 8px;
    border: 2px solid;
    border-color: #4472C4;
}
  
.main-legend {
    text-align: left;
    font-size: 19px;
    font-weight: bold;
    color: #4472C4;
}
  
.left {
    text-align: left;
    padding-top: 2px;
}
    
button {
    width: 96px;
    padding: 4px;
    background-color: #4472C4;
    color: #b3d1ff;
    border: 1px solid;
    border-color: #b3d1ff;
    font-weight: bold;
    font-size: 19px;
    border-radius: 7px;
}

button:hover {
    color: white;
}
  
h1 {
    color: #4472C4;
}

.input {
    padding: 1px;
}
  
.flex {
    display: flex;
}
  
.center {
    margin: auto;
    align-content: center;
    width: 90%;
}

#main {
    display: none;
    width: 960px;
}
  
#status {
    font-size: 14px;
    color: #4472C4;
}
  
#data {
    color: #4472C4;
}

textarea {
    border-color: #4472C4;
    resize: none;
    border-radius: 5px;
}

#data:focus, #result:focus {
    outline-color: #4472C4;
}