.login-fieldset {
    border-radius: 8px;
    border: 2px solid;
    border-color: #4472C4;
    /* box-shadow: 2px 2px 4px #1172C4; */
}

.login-legend {
    color: #4472C4;
    text-align: left;
    font-weight: bold;
    font-size: 19px;
}

.login {
    padding: 2px;
}

.login-button {
    padding-top: 16px;
    padding-bottom: 8px;
}

#login-btn {
    background-color: #4472C4;
    color: #b3d1ff;
    border: 1px solid;
    border-color: #b3d1ff;
    font-weight: bold;
    font-size: 19px;
    width: 128px;
}

#login-btn:hover {
    color: white;
}

.login-form {
    padding-top: 96px;
    width: 560px;
    margin: auto;
}

#login-error {
    font-size: 14px;
    color: red;
}

#logo-img {
    padding-top: 28px;
    padding-bottom: 8px;
}

#buttons {
    padding-top: 16px;
    padding-bottom: 4px;
}

input::placeholder {
    color: #4472C4;
}

#username {
    border: 1px solid;
    border-color: #4472C4;
    color: #4472C4;
    font-size: 16px;
    border-radius: 5px;
}

#password {
    border: 1px solid;
    border-color: #4472C4;
    color: #4472C4;
    font-size: 16px;
    border-radius: 5px;
}

#username:focus, #password:focus {
    outline: none;
}
